.header{grid-column:span 2}.сontainer{background-color:#000;padding:0.625rem 1.375rem;height:100%;display:flex;align-items:center;justify-content:space-between}@media(max-width: 47.99875em){.сontainer{padding:0.625rem 0.625rem}}.logo{width:120px}@media(max-width: 47.99875em){.logo{width:100px}}@media(max-width: 29.24875em){.logo{width:80px}}.logo img{width:100%}.actions{display:flex;gap:1.75rem;align-items:center}@media(max-width: 47.99875em){.actions{gap:0.875rem}}.actions__auth{cursor:pointer;color:#fff;font-size:1.5625rem;font-weight:500}.actions__auth :hover{text-decoration:underline}@media(max-width: 47.99875em){.actions__auth{font-size:1.25rem}}@media(max-width: 29.24875em){.actions__auth{font-size:1rem}}